import React,{Component} from 'react';
import { Server } from "net";
import { strict } from 'assert';
import { stringify } from 'querystring';
import './Box1.css';
 

class Box1 extends Component {
    constructor(props){
        super(props)
        this.state = {
            selectValue : 0 , 
            selectDate : "",
            newday: "",
            newdate : "",
            newmonth:"",
            newyear:"",
            arrmonth : ['January','February','March','April','May','June','July','August','September','October','November','December'],
            arrdays : ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'],
            newdatestring:"",
            submitted:false,
            f_day :"",
            f_date :"",
            f_month : "",
            f_year:"",


        };
        this.handleDropdownChange = this.handleDropdownChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
    }

    handleDropdownChange(e) {
        this.setState({ selectValue: e.target.value });
      }
    handleClick = (p) => {
        var temp = new Date((this.state.selectDate));
        var interval = Number(this.state.selectValue);
        var fertility_temp = new Date();
        var more_fertility = new Date();

        temp.setDate((temp.getDate()+interval)-14);
        fertility_temp.setDate(temp.getDate()-3);
        more_fertility.setDate(temp.getDate()+2);
        this.setState({newdate: temp.getDate(),newmonth:temp.getMonth(),newyear:temp.getFullYear(),newday:temp.getDay()});
        this.setState({f_day:fertility_temp.getDay(),f_date : fertility_temp.getDate(),f_month:fertility_temp.getMonth(),f_year : fertility_temp.getFullYear()})
        this.setState({m_day:more_fertility.getDay(),m_date : more_fertility.getDate(),m_month:more_fertility.getMonth(),m_year : more_fertility.getFullYear()})

        console.log(temp + "hello"+ fertility_temp);

        var month = temp.getMonth()+1;
        var date = temp.getDate();
        if(month<10){
          month = "0"+ month;
        }
        if(date<10){
          date = "0"+ date;
        }

        let temp_str =  temp.getFullYear()+"-"+month+"-"+date;
        console.log(temp_str)
        this.setState({newdatestring:temp_str,submitted:true});


        
    }

    

    render(){
        return(
            <div className ="mainbox">
            <div className="container">
                <div className="row" >
                    <h1 style={{textAlign:"center",color:"#163948",paddingTop:"5%", paddingBottom:"3%",paddingLeft:"35%"}}>Ovulation Calculator</h1>
                </div>
                <div className="row"> 
                  <div className="col-sm-6">
                    <p  style={{marginLeft:"25%" ,color:"#163948" ,paddingLeft:"4%"}}><strong>Enter the start date of period</strong> </p>
                  </div>
                  <div className="col-sm-6">
                    <p style={{marginLeft:"10%" ,color:"#163948" ,paddingLeft:"4%"}}><strong>Enter average interval between two menstural cycles</strong></p>
                  </div>
                    
                </div>
                <div className="row">
                <div className="col">
                        
                        <input className="inputDate"  type="date" onChange={(event) => this.setState({selectDate: event.target.value})}/>
                    </div>
                    <div className="col">
                        <select style={{marginLeft:"33%", width : "200px", height:"50px" ,color:"#127681" ,borderRadius:"25px",paddingLeft:"4%",borderWidth:"5px"}} id ="dropdown" onChange={this.handleDropdownChange}>
                                    <option value="">Select days</option>
                                    <option value="20">20 days</option>
                                    <option value="21">21 days</option>
                                    <option value="22">22 days</option>
                                    <option value="23">23 days</option>
                                    <option value="24">24 days</option>
                                    <option value="25">25 days</option>
                                    <option value="26">26 days</option>
                                    <option value="27">27 days</option>
                                    <option value="28">28 days</option>
                                    <option value="29">29 days</option>
                                    <option value="30">30 days</option>
                                    <option value="31">31 days</option>
                                    <option value="32">32 days</option>
                                    <option value="33">33 days</option>
                                    <option value="34">34 days</option>
                                    <option value="35">35 days</option>
                                    <option value="36">36 days</option>
                                    <option value="37">37 days</option>
                                    <option value="38">38 days</option>
                                    <option value="39">39 days</option>
                                    <option value="40">40 days</option>
                                    <option value="41">41 days</option>
                                    <option value="42">42 days</option>
                                    <option value="43">43 days</option>
                                    <option value="44">44 days</option>
                                    <option value="45">45 days</option>
                            </select>

                    </div>
                    
                </div>
                <div className="row">
                    <button className="myButton" style={{marginLeft:"45%",marginTop:"2%"}} onClick ={()=> this.handleClick()}>Submit</button> 
                </div>
                  <div >

                      {this.state.submitted?<div style={{backgroundColor:"#edffea",marginTop:"5%",paddingBottom:"3%",borderRadius:"28px"}}>
                        <div className="row">
                              <div className="col-md-6">
                              <h4 style={{textAlign:"center",paddingTop:"10%",color:"#54595F"}}>Your Ovulation Date is : {this.state.arrdays[this.state.newday]}, {this.state.newdate} {this.state.arrmonth[this.state.newmonth]} {this.state.newyear}</h4>
                              </div>
                              <div className = "col-md-6">
                                  <p style={{marginLeft:"45%",marginTop:"0%"}}>View Ovulation Date in Calendar</p>
                                  <input className="inputDate" style={{marginLeft:"45%",marginTop:"0%"}} type="date" value ={this.state.newdatestring} />
                                 
                              </div>
                          </div>
                        
                          
                         
                          <h3 style={{textAlign:"center",paddingTop:"5%",color:"#54595F"}}>Most Fertile Days : {this.state.arrdays[this.state.f_day]}, {this.state.f_date} {this.state.arrmonth[this.state.f_month]} {this.state.f_year} to {this.state.arrdays[this.state.newday]}, {this.state.newdate} {this.state.arrmonth[this.state.newmonth]} {this.state.newyear}</h3>

                          <h3 style={{textAlign:"center",paddingTop:"5%",color:"#54595F"}}>More Possible Days : {this.state.arrdays[this.state.newday]}, {this.state.newdate} {this.state.arrmonth[this.state.newmonth]} {this.state.newyear} to {this.state.arrdays[this.state.m_day]}, {this.state.m_date} {this.state.arrmonth[this.state.m_month]} {this.state.m_year}</h3>
                        
                          <div className="row">
                              <a style={{marginLeft:"45%",marginTop:"2%"}} className="myButton" href="/" >Redo</a>
                          </div>

                          <div>
                            
                          </div>
                      </div>:null}

                </div>
                
            </div>
            
            </div>
        )
    }
} 

export default Box1

